body {
  font-family: Monospace;
  background-color: #fff; /* will show through only if renderer alpha: true */
  margin: 0px;
  padding: 0px;
  overflow: hidden;
}
h3{
  margin-top: 0;
}
#info {
  text-align: center;
  color: #000;
  position: absolute;
  top: 0px;
  width: 100%;
  padding: 5px;
}

input {
  display: inline-block;
  width: 70px;
  padding: 5px;
}

#set span {
  padding-right: 20px;
}

#set button {
  margin-top: 15px;
  padding: 5px;
}

#inset {
  width: 100px;
  height: 100px;
  background-color: #fff; /* or transparent; will show through only if renderer alpha: true */
  border: 1px solid black; /* or none; */
  margin: 15px;
  padding: 0px;
  position: absolute;
  right: 0px;
  bottom: 0px;
  z-index: 100;
}

#set {
  width: auto;
  height: auto;
  background-color: #fff; /* or transparent; will show through only if renderer alpha: true */
  border: 1px solid black; /* or none; */
  margin: 10px;
  padding: 15px;
  position: absolute;
  right: 0px;
  top: 0px;
  text-align: center;
  z-index: 100;
}

#stats {
  width: 330px;
  height: auto;
  background-color: #fff; /* or transparent; will show through only if renderer alpha: true */
  border: 1px solid black; /* or none; */
  margin: 15px;
  padding: 0px;
  position: absolute;
  left: 0px;
  top: 0px;
  text-align: center;
  z-index: 100;
}
.n {
  width: 70px;
}

@media only screen and (max-width: 700px) {
  #inset {
    display: none;
  }
  #stats {
    margin: 0;
    width: 100vw;
  }
  #set {
    bottom: 0;
    left: 0;
    margin: 0;
    height: auto;
    top: inherit;
    width: calc(100vw - 10px);
    padding: 5px;
  }
}
